import Vue from 'vue'
import Router from 'vue-router'
import qindex from '@/pages/q-index/qindex'
import qpic from '@/pages/q-index/qpic'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'qindex',
      component: qindex,
      children:[
        {
          path:"",
          name:"qpic",
          component:qpic
        }
      ]
    }
  ]
})



